<script>
import Layout from "../../layouts/main";
import PageHeader from "@/components/page-header";
import appConfig from "@/app.config";
import axios from 'axios';
/**
 * Form-element component
 */
/**
 * Written by 张文彧
 */
export default {
  page: {
    title: "个人信息",
    meta: [{ name: "description", content: appConfig.description }]
  },
  components: { Layout, PageHeader },
  data() {
    return {
		//是否开始编辑
		userName:'',
		ifEdit:false,
		form: {
			id:'',
		    userName: '',
			userImgAddr:'https://cube.elemecdn.com/e/fd/0fc7d20532fdaf769a25683617711png.png',
		    userPasswrod: '',
		    userStatus: '',
		    userAuth: '',
		    userPhone: '',
		    userEmail: '',
		    lastLoginTime: '',
		    departmentId: '',
		},
      title: "个人信息",
      items: [
        {
          text: "主页",
          href: "/"
        },
        {
          text: "个人信息",
          active: true
        }
      ],
      status: "not_accepted",
      checkCustom: "not_accepted",
      checked: true
    };
  },
  methods:{
	  edit(){
		  console.log(this.ifEdit),
		  this.ifEdit=!this.ifEdit
	  },
	  onSubmit() {
		axios
			.post('http://localhost:8848/users/updateById',this.form )
			.then(response => (
				// this.userList = response.data.list
				console.log(response),
				this.$message({
					type: 'success',
					message: '修改成功!'
				}),
				this.getInfo(),
				this.ifEdit=false
				
			))
			.catch(function(error) { // 请求失败处理
				console.log(error);
			});
	          console.log('submit!');
	  },
	  getInfo(){
		  axios
		  	.get('http://localhost:8848/users/selectByUsername',{params:{'username':this.userName}} )
		  	.then(response => (
		  		// this.userList = response.data.list
		  		this.form.id=response.data.id,
		  		this.form.userName=response.data.userName,
		  		this.form.userEmail=response.data.userEmail,
				this.form.userPasswrod=response.data.userPasswrod,
		  		this.form.userPhone=response.data.userPhone,
		  		this.form.lastLoginTime=response.data.lastLoginTime,
		  		this.form.userStatus=response.data.userStatus,
		  		this.form.userAuth=response.data.userAuth,
		  		this.form.departmentId=response.data.departmentId
		  	))
		  	.catch(function(error) { // 请求失败处理
		  		console.log(error);
		  	});
	  }
  },
  created() {
	  /**
	   * 根据username查询个人信息
	   */
	  // let username = sessionStorage.getItem('username')
	  this.userName=sessionStorage.getItem('username'),
	this.getInfo()
  },
};
</script>

<template>
  <Layout>
    <PageHeader :title="title" :items="items" />
    <el-form ref="form" :model="form" label-width="100px">
		<el-form-item label="头像">
		  <div class="block"><el-avatar :size="50" :src="form.userImgAddr"></el-avatar></div>
		</el-form-item>
	
      <el-form-item label="用户名"  style="width: 70%;">
        <el-input readonly v-model="form.userName"></el-input>
      </el-form-item>
	  <el-form-item label="手机号" style="width: 70%;">
	    <el-input :readonly="!ifEdit" v-model="form.userPhone"></el-input>
	  </el-form-item>
	  <el-form-item label="邮箱" style="width: 70%;">
	    <el-input :readonly="!ifEdit" v-model="form.userEmail"></el-input>
	  </el-form-item>
	  <el-form-item label="最后登录时间" v-show="!ifEdit" style="width: 60%;">
	    <el-input  v-model="form.lastLoginTime" readonly></el-input>
	  </el-form-item>
	  <el-form-item label="部门ID" v-show="!ifEdit" style="width: 50%;">
	    <el-input  v-model="form.departmentId" readonly></el-input>
	  </el-form-item>
	  <el-form-item label="用户状态"  v-show="!ifEdit" style="width: 50%;">
	    <el-input  v-model="form.userStatus" readonly></el-input>
	  </el-form-item>
	  <el-form-item label="用户权限" v-show="!ifEdit" style="width: 50%;">
	    <el-input  v-model="form.userAuth" readonly></el-input>
	  </el-form-item>
	  <!-- 
      <el-form-item label="活动区域">
        <el-select v-model="form.region" placeholder="请选择活动区域">
          <el-option label="区域一" value="shanghai"></el-option>
          <el-option label="区域二" value="beijing"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="活动时间">
        <el-col :span="11">
          <el-date-picker type="date" placeholder="选择日期" v-model="form.date1" style="width: 100%;"></el-date-picker>
        </el-col>
        <el-col class="line" :span="2">-</el-col>
        <el-col :span="11">
          <el-time-picker placeholder="选择时间" v-model="form.date2" style="width: 100%;"></el-time-picker>
        </el-col>
      </el-form-item>
      <el-form-item label="即时配送">
        <el-switch v-model="form.delivery"></el-switch>
      </el-form-item>
      <el-form-item label="活动性质">
        <el-checkbox-group v-model="form.type">
          <el-checkbox label="美食/餐厅线上活动" name="type"></el-checkbox>
          <el-checkbox label="地推活动" name="type"></el-checkbox>
          <el-checkbox label="线下主题活动" name="type"></el-checkbox>
          <el-checkbox label="单纯品牌曝光" name="type"></el-checkbox>
        </el-checkbox-group>
      </el-form-item>
      <el-form-item label="特殊资源">
        <el-radio-group v-model="form.resource">
          <el-radio label="线上品牌商赞助"></el-radio>
          <el-radio label="线下场地免费"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="活动形式">
        <el-input type="textarea" v-model="form.desc"></el-input>
      </el-form-item> -->
      <el-form-item v-show="!ifEdit">
        <el-button type="primary" @click="edit()" >修改信息</el-button>
      </el-form-item>
	  <el-form-item v-show="ifEdit"> 
	    <el-button type="primary" @click="onSubmit" style="margin">确认</el-button>
	    <el-button @click="edit()" >取消</el-button>
	  </el-form-item>
    </el-form>
    <!-- end row -->

    <!-- end row -->
  </Layout>
</template>